body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  /* background-color: #faf7f7; */
  background-color: #fffcf7;
  width: 100vw;
  height: 100vh;
  box-shadow: inset -35px 0px 45px 0px #efeae0;
  display: flex;
}

.aa {
  width: 99%;
  height: 99%;
  cursor: none;
}

.navbar {
  display: flex;
  flex-direction: column;
  width: 10vw;
  padding: 3vh;
  margin-right: 3vw;
  box-shadow: 10px 10px 5px 0px rgba(239, 234, 224, 1);
  transition: 0.3s;
}

.navbar > a {
  font-size: calc(20px + (14 - 12) * (100vw - 600px) / (1920 - 600));
  font-family: noto_serifregular, serif;
  font-weight: 400;
  color: #dd7a0e;
  text-decoration: none;
}
.navbar:hover {
  box-shadow: 10px 10px 5px 0px #f1e5d2;
}

